@tailwind base;
@tailwind components;
@tailwind utilities;


html, body {
  margin: 0;
  padding: 0;
  height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root {
  --max-width : 1200px;

  /* 메뉴관련 색상 변수 */
  --menucolor-line : #000000;
  --menucolor-text : #000000;
  --menucolor-bg : #ffffff;
  --menucolor-active : #70BAFF;
  --menucolor-nonactive : black;

  /* 메인베너 관련 */
  --mainbanner-active : #70BAFF;
  --mainbanner-background1 : #F3F3F3;
  --mainbanner-background2 : #70BAFF;
  --mainbanner-fontcolor1 : #F3F3F3;

  /* 섹션관련 */
  --section-active : #70BAFF;
  --section-nonactive : #F3F3F3;
  --section-bg : #EBFAFF;
  --section-color : #ffffff;
  --section-line : #000000;

/* 메인배너, 활성화, 아이콘 바탕색 등 : 70BAFF
메뉴바탕색 : FFFFFF
메뉴 글자색 : 000000
섹션버튼 활성화 비활성화 글자색 : FFFFFF
섹션버튼 활성화 바탕색 : EBFAFF
섹션버튼 비활성화 바탕색 : F3F3F3 */

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}