html, body {
  margin: 0;
  padding: 0;
  height: 100vh;
}

body.TravelMap {
  overflow: hidden;
}

.Map_PC .Tside-bar{
  position: absolute;
  top: 122px;
  left: 0;
  right: auto;
  width: 400px;
  bottom : 0;
  transition: left 1s;
  z-index: 998;
}

.Map_PC .Tside-bar.Print{
  left:-350px;
}

.Map_PC .Tside-bar .Tside-bar_tourlist > div:first-child {
  height: 40px;
  border-radius: 15px;
  border : 2px solid var(--menucolor-line);
  align-items: center;
  font-size: 16px;
  margin: 10px auto;
  padding: 0 10px;
  width: 340px;
}

.Map_PC .Tside-bar .Tside-bar_tourlist > div:first-child input {
  border: none;
  height: 35px;
  width : 300px;
}

.Map_PC .Tside-bar .Tside-bar_tourlist > div:first-child img {
  height: 30px;
  margin-left: 10px;
}

.Map_PC .Tside-bar .Tside-bar_tourlist > ul li {
  height: 140px;
  padding: 20px 10px 10px 10px;
  border-bottom: 1px solid black;
  background-color: white;
}

.Map_PC .Tside-bar .Tside-bar_tourlist > ul li:hover {
  background-color: lightgray;
}

.Map_PC .Tside-bar .Tside-bar_tourlist > ul li div{
  pointer-events: none;
}

.Map_PC .Tside-bar .Tside-bar_list .Detaildesc{
  display: none;
  position: absolute;
  top: 50px;
  left : 420px;
  width: 300px;
  bottom: 50px;
  overflow-y: auto;
  background-color: whitesmoke;
  padding: 20px;
  border-radius: 10px;
  z-index: 998;
}

/* 상세설명 출력을 위함 */
.Map_PC .Tside-bar .Tside-bar_list .Detaildesc.Print{
  display: flex;
}

.Map_PC .Tside-bar .Tside-bar_list .Detaildesc .Detaildesc_Context > div{
  width : 100%;
  max-height: 80px;
  text-align: left;
  overflow-y: hidden;
  margin-bottom: 20px;
}

/* 더보기 속성을 위함 */
.Map_PC .Tside-bar .Tside-bar_list .Detaildesc .Detaildesc_Context > div.Print{
  max-height: 100%;
}

.Map_PC .Tside-bar .Tside-bar_list .Detaildesc .Detaildesc_Context {
  white-space: pre-line;
}

.Map_PC .Tside-bar .Tside-bar_tourlist {
  overflow-y: auto;
}

.Map_PC .Tside-bar .Tside-bar_route{
  position: absolute;
  top: 0;
  left: 0;
  right: auto;
  width: 380px;
  bottom : 0;
  background-color: white;
  z-index: 998;
  overflow-y: auto;
}

.Map_PC .DetailImg {
  position: absolute;
  top : 150px;
  left : 30px;
  right : 30px;
  bottom : 30px;

  border-radius: 20px;
  padding: 20px;
  background-color: #F3F3F3;
  z-index: 998;
  display: none;
}
  
.Map_PC .DetailImg > div {
  padding: 10px 0;
  overflow-y: auto;
}

.Map_PC .DetailImg > div > ul > li {
  width : 400px;
  padding: 10px;
  display: inline-block;
}

.Map_PC .DetailImg.Print{
  display: flex;
}

.Map_PC .Tside-bar .Tside-bar_bar {
  width: 20px;
  background-color: lightblue;
}

.Map_PC .Map_Image{
  position: absolute;
  top: 122px;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
}

.Map_PC .Map_Image.Print{
  left: 300px;
  right: 0;
}

/* Tmap */
/* 거리표시 팝업*/
.Map_PC .Map_Image .vsm-container:first-child .mPop{
  border: 1px;
  background-color: #FFF;
  font-size: 12px;
  border-color: #FF0000;
  border-style: solid;
  text-align: center;
}
/*공통사용 클래스*/
.Map_PC .Map_Image .vsm-container:first-child .mPopStyle {
  border: 1px;
  background-color: #FFF;
  font-size: 12px;
  border-color: #FF0000;
  border-style: solid;
  text-align:left;
}

/* 모바일 */
.Map_MB{
  height: 100vh;
}


@media only screen and (max-width: 900px) {
  .Map_PC{
    display: none ;
  }
  .Map_MB {
      display: flex;
  }
}

@media only screen and (min-width: 901px) {
  .Map_PC{
    display: flex;
  }
  .Map_MB{
    display: none;
  }
}