html, body {
  margin: 0;
  padding: 0;
  height: 100%;
}

.con {
  max-width: var(--max-width);
  margin: 0 auto;
}

/* pc */
.Customer_PC .Main-Banner{
  height: 450px;
  background-color: var(--mainbanner-active);
  position: relative;
}

.Customer_PC .Main-Banner_Main{
  width : 800px;
}

.Customer_PC .Main-Banner_Main_Img>img{
  height: 300px;
  margin-top: 30px;
}

.Customer_PC .Main-Banner_Main_Text{
  color : var(--mainbanner-background1);
  font-weight: bold;
  font-size: 18px;
}

.Customer_PC .Main-Banner_Main_Text > div:first-child{
  font-size: 32px;
  margin-top: 50px;
}

.Customer_PC .Main-Banner_Main_Text a {
  padding : 10px;
  border-radius: 30px;
  background-color : var(--mainbanner-background1 );
  color : black;
}

.Customer_PC .Sections{
  background-color: var(--section-bg);
  padding-top: 50px;
  padding-bottom: 70px;
}

.Customer_PC .Sections_List{
  width : 1000px;
}

.Customer_PC .Sections_List > div:first-child{
  font-size: 30px;
  font-weight: bold;
  margin-bottom: 20px;
}

/* mobile */
.Customer_MB .Main-Banner{
  height: 450px;
  background-color: var(--mainbanner-active);
  position: relative;
}

.Customer_MB .Main-Banner_Main{
  margin-top: 20px;
  padding: 20px 40px 40px 40px;
}

.Customer_MB .Main-Banner_Main_Img{
  width: 80%;
}

.Customer_MB .Main-Banner_Main_Text{
  color: var(--mainbanner-background1);
  font-size: 18px;
  font-weight: bold;
}

.Customer_MB .Main-Banner_Main_Text > div:first-child{
  font-size: 32px;
  margin-top: 10px;
}

.Customer_MB .Main-Banner_Main_Text a {
  padding : 15px;
  border-radius: 10px;
  background-color : var(--mainbanner-background1 );
  color: black;
}

.Customer_MB .Sections{
  /* height: 1050px; */
  background-color: var(--section-bg);
  padding: 20px 20px 30px 20px;
}

.Customer_MB .Sections_List{
  text-align: start;
}

.Customer_MB .Sections_List > div:first-child{
  font-size: 30px;
  font-weight: bold;
  margin-bottom: 20px;
}

@media only screen and (max-width: 900px) {
  .Customer_PC{
    display: none ;
  }
  .Customer_MB {
      display: flex;
  }
}

@media only screen and (min-width: 901px) {
  .Customer_PC{
    display: flex;
  }
  .Customer_MB{
    display: none;
  }
}