html, body {
  margin: 0;
  padding: 0;
  height: 100%;
}

.con {
  max-width: var(--max-width);
  margin: 0 auto;
}

/* pc */
.Home_PC .Main-Banner{
  height: 500px;
  background-color: var(--mainbanner-active);
  position: relative;
  margin : auto;
}

.Home_PC .Main-Banner_Main{
  width : 800px;
  margin: auto;
}

.Home_PC .Main-Banner_Main_Img>img{
  height: 300px;
  margin-top: 30px;
}

.Home_PC .Main-Banner_Main_Text{
  color : var(--mainbanner-background1);
  font-weight: bold;
  font-size: 18px;
}

.Home_PC .Main-Banner_Main_Text > div:first-child{
  font-size: 32px;
  margin-top: 50px;
}

/* 섹션파트 */
.Home_PC .Sections{
  background-color: var(--section-bg);
  padding-top: 50px;
  padding-bottom: 70px;
}

.Home_PC .Section1_Title{
  font-size: 30px;
  font-weight: bold;
}

.Home_PC .Section1_TabBox{
  background-color: white;
  border-radius: 10px;
}

.Home_PC .Section1_TabBox_Tab1 {
  background-color: var(--section-active);
  border-radius: 10px 10px 0 0;
}

.Home_PC .Section1_TabBox_Tab1_bar >div{
  padding: 10px 0;
}

.Home_PC .Section1_TabBox_Tab1 button{
  color : white;
  font-size: 18px;
  font-weight: bold;
}

.Home_PC .Section1_TabBox_Tab2 button{
  font-size: 16px;
  font-weight: bold;
}

.Home_PC .Section1_TabBox_Tab2-Pannel{
  font-size: 22px;
  font-weight: bold;
}

.Home_PC .Section1_TabBox_Tab2-Pannel span{
  color: var(--section-active);
  font-size: 26px;
}

/* mobile */

.Home_MB .Main-Banner{
  height: 400px;
  background-color: var(--mainbanner-active);
  position: relative;
  margin : auto;
}

.Home_MB .Main-Banner_Main{
  margin: auto;
  padding: 40px;
}

/* .Home_MB .Main-Banner_Main_Img>img{
  width: 80%;
  margin-top: 30px;
  margin-left: auto;
  margin-right: auto;
} */

.Home_MB .Main-Banner_Main_Text{
  color : var(--mainbanner-background1);
  font-weight: bold;
  font-size: 18px;
}

.Home_MB .Main-Banner_Main_Text > div:first-child{
  font-size: 32px;
  margin-top: 50px;
}

/* 섹션파트 */
.Home_MB .Sections{
  background-color: var(--section-bg);
  padding: 50px 15px 70px 15px;
}

.Home_MB .Section1_Title{
  font-size: 30px;
  font-weight: bold;
}

.Home_MB .Section1_TabBox{
  background-color: white;
  border-radius: 10px;
}

.Home_MB .Section1_TabBox_Tab1 {
  background-color: var(--section-active);
  border-radius: 10px 10px 0 0;
}

.Home_MB .Section1_TabBox_Tab1_bar >div{
  padding: 5px 0;
}

.Home_MB .Section1_TabBox_Tab1 button{
  color : white;
  font-size: 18px;
  font-weight: bold;
}

.Home_MB .Section1_TabBox_Tab2 button{
  font-size: 16px;
  font-weight: bold;
}

.Home_MB .Section1_TabBox_Tab2-Pannel{
  font-size: 22px;
  font-weight: bold;
}

.Home_MB .Section1_TabBox_Tab2-Pannel span{
  color: var(--section-active);
  font-size: 26px;
}

@media only screen and (max-width: 900px) {
  .Home_PC{
    display: none ;
  }
  .Home_MB {
    display: block;
  }
}

@media only screen and (min-width: 901px) {
  .Home_PC{
    display: block;
  }
  .Home_MB{
    display: none;
  }
}