html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  position: relative;
}

ul, li, a {
  margin: 0;
  padding: 0;
}

/* 메뉴파트 */
/* pc버전 */
.Map_PC .Top-Menu_PC {
  color : var(--menucolor-text);
  font-size: 18px;
  background-color: var(--menucolor-bg);
}

.Top-Menu_PC > ul {
  height: 60px;
  margin : 0 80px;
}

.Top-Menu_PC > .Top-Menu_Upper > li {
  align-items: center;
}

.Top-Menu_PC > .Top-Menu_Upper > li:nth-child(1) img {
  height: 40px;
}

.Top-Menu_PC > .Top-Menu_Upper > li:nth-child(2) >div {
  width: 400px;
  height: 40px;
  border-radius: 15px;
  border : 2px solid var(--menucolor-line);
  margin-left: 40px;
  padding-left : 24px;
  align-items: center;
  font-size: 16px;
}

.Top-Menu_PC > .Top-Menu_Upper .Top-Menu_upper-Input input {
  border: none;
  height: 35px;
}

.Top-Menu_PC > .Top-Menu_Upper > li:nth-child(4) > a {
  width: 50px;
  height: 100%;
  align-items: center;
  justify-content: center;
}

.Top-Menu_PC > .Top-Menu_Lower {
  max-width: var(--max-width);
  margin : 0 auto;
}

.Top-Menu_PC > .Top-Menu_Lower > li {
  width: 300px;
  justify-content: center;
  align-items: center;
  padding : 0 20px;
}

.Top-Menu_PC > .Top-Menu_Lower > li > a {
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  position: relative;
}

.Top-Menu_PC > .Top-Menu_Lower > li > a::after {
  content: "";
  position: absolute;
  bottom : 0;
  left : 50;
  width: 0;
  height: 4px;
  opacity: 0;
  background-color: var(--menucolor-active);
  transition : width .3s, opacity .5s;
}

.Top-Menu_PC > .Top-Menu_Lower > li:hover > a::after {
  width: 70%;
  opacity: 100%;
}

.Top-Menu_PC > hr {
  border : 0.5px solid var(--menucolor-line);
}

.Top-Menu_PC .mark{
  height:35px; 
  margin-right: 10px;
}

.Top-Menu_PC .mark_log{
  height: 45px;
}

.Top-Menu_PC .mark_join{
  height: 30px;
  margin-top: 3px;
}

.Top-Menu_PC .mark_search{
  height: 25px;
  margin-right: 10px;
}

/* 모바일 버전 */
.Top-Menu_MB {
  color : var(--menucolor-text);
  font-size: 18px;
  background-color: var(--menucolor-bg);
}

.Top-Menu_MB > ul {
  height: 60px;
  margin : 0 20px;
}

.Top-Menu_MB > .Top-Menu_Upper > li {
  align-items: center;
}

.Top-Menu_MB > .Top-Menu_Upper > li img {
  height: 40px;
}

.Top-Menu_MB > .Top-Menu_Upper .MB-acodian {
  position : absolute;
  top: 0;
  left: -80%;
  width: 80%;
  height: 100vh;
  background: white;
  transition: left 0.5s;
}

.Top-Menu_MB > .Top-Menu_Upper .MB-acodian.INPUT {
  left:0%;
}

.Top-Menu_MB > .Top-Menu_Upper .MB-acodian Accordion {
  width: 100%;
  /* margin: 0; */
}

.Top-Menu_MB > .Top-Menu_Upper .MB-acodian a{
  width: 100%;
  height: 100%;
}

.Top-Menu_MB > .Top-Menu_Lower > li >div {
  height: 40px;
  border-radius: 15px;
  border : 1px solid var(--menucolor-line);
  padding-left : 20px;
  align-items: center;
  font-size: 16px;
}

.Top-Menu_MB > .Top-Menu_Lower > li  a {
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  position: relative;
}

.Top-Menu_MB > hr {
  border : 0.5px solid var(--menucolor-line);
}

.Top-Menu_MB .mark{
  height:100%; 
  font-size: 36px;
}

.Top-Menu_MB .mark_search{
  height: 25px;
  margin-right: 10px;
}

.Top-Menu_MB .mark_log{
  height: 60px;
}

.Top-Menu_MB .mark_join{
  height: 30px;
  margin-top: 3px;
}

@media only screen and (max-width: 900px) {
  .Top-Menu_PC{
    display: none;
  }
  .Top-Menu_MB{
    display: flex;
  }
}

@media only screen and (min-width: 901px) {
  .Top-Menu_PC{
    display: flex;
  }
  .Top-Menu_MB{
    display: none;
  }
}