.Join_PC{
    height: 70vh;
    background-color: var(--mainbanner-background2);
    color: var(--mainbanner-fontcolor1);
}

.Join_PC .Join_InfoBox {
    height: 40vh;
    margin : auto;
}

.Join_PC .Join_InfoBox_Message {
    font-size: 42px;
    font-weight: bold;
    padding-top: 30px;
    padding-bottom: 20px;
}

.Join_PC .Join_InfoBox_List > div {
    font-size: 22px;
}

.Join_PC .Join_InfoBox_List > div:first-child {
    font-size: 32px;
    font-weight: bold;
    margin-bottom: 20px;
}

/* 모바일 */
.Join_MB{
    height: 80vh;
    background-color: var(--mainbanner-background2);
    color: var(--mainbanner-fontcolor1);
}

.Join_MB .Join_InfoBox {
    height: 40vh;
    margin-top: 8vh;
    margin-left: auto;
    margin-right: auto;
    /* margin-bo: auto; */
}

.Join_MB .Join_InfoBox_Message {
    font-size: 38px;
    font-weight: bold;
    padding-top: 5vh;
    padding-bottom: 10vh;
}

.Join_MB .Join_InfoBox_Message > div:first-child {
    font-size: 52px;
}

.Join_MB .Join_InfoBox_List > div {
    font-size: 22px;
}

.Join_MB .Join_InfoBox_List > div:first-child {
    font-size: 32px;
    font-weight: bold;
    margin-bottom: 3vh;
}

@media only screen and (max-width: 900px) {
    .Join_PC{
      display: none ;
    }
    .Join_MB {
        display: flex;
    }
  }

  @media only screen and (min-width: 901px) {
    .Join_PC{
      display: flex;
    }
    .Join_MB{
      display: none;
    }
  }
  


